.container{height:100vh;display:flex;flex-direction:column;justify-content:center;align-items:center}.container .wheel{width:16rem;animation:drive 1.5s infinite linear}@keyframes drive{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}.container p{margin-top:3rem;font-size:3rem;color:#319fff;font-weight:400}